<template>

    <div class="el">

        <div class="p-2 title is-3">
            Tasks <span class="tag"> {{ tasks.length }} </span>

        </div>


        <div v-for='(task, id) in tasks' :key='id' class="taski m-1">

            <label class="radio ">
                <input type="radio" name='currentTaks' :id='task'>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                {{ task }}
            </label>
            <div class="task-icons">
                <i @click.prevent='delIt(id)' class="far fa-trash-alt"></i>
                <i @click.prevent='editIt(id)' class="far fa-edit"></i>
            </div>


        </div>


        <div v-if='!addin&&!editing' @click='showAddingForm' class="task-creator px-4 m-2">
            <i class="fas fa-plus myIcon"></i>
            Add in here a new task
        </div>

        <!-- Here, if we editing... -->
        <div v-else-if='!addin&&editing' class="container p-1">
            <form action="">
                <input id='editTask' class="input" v-on:keydown.enter.prevent='add' v-model='task' type="text" placeholder="Describe your task...">
                <div class="buttons mt-1">
                    <div
                        @click='saveEdit'
                        v-bind:disabled = 'task.length < 2'
                        id='button_2'
                        class="button is-success"
                    >
                        Acept
                    </div>
                    <div @click='showAddingForm' class="button">Cancel</div>
                </div>
            </form>
        </div>

        <!-- Here we will add  the div for our new TASK -->
        <div v-else class="container p-1">
            <form action="">
                <input id='currentTask' class="input" v-on:keydown.enter.prevent='add' v-model='task' type="text" placeholder="Describe your task...">
                <div class="buttons mt-1">
                    <div
                        @click='add'
                        v-bind:disabled = 'task.length < 2'
                        id='button_1'
                        class="button is-success"
                    >
                        Acept
                    </div>
                    <div @click='showAddingForm' class="button">Cancel</div>
                </div>
            </form>

        </div>


    </div>

</template>

<script>
    export default {
        name: 'taskList',
        data(){
            return{
                /* Are we adding? */
                addin: false,
                /* What are we adding? */
                task: '',
                tasks: [
                    'Name it',
                    'let',
                    'me',
                    'try'
                ],
                /* Are we editing? */
                editing: false,
                /* what are we editing */
                editId: null

        }
        },
        methods:{
            saveEdit(){
                /* this.tasks = this.tasks.splice(this.editId, 1, this.task);
                this.task = ''; */
                console.log(this.tasks);
                this.tasks.splice(this.editId, 1, this.task);
                console.log(this.tasks);
                this.editing = false;
                this.addin = false
            }
            ,
            editIt(id){
                this.editId = id;
                this.editing = true;
                this.task = this.tasks[id];
                let editted = document.getElementById('editTask');
                console.log(editted);
                if(editted){
                    editted.value = this.tasks[id];
                }




            },
            delIt(id){
                this.tasks = this.tasks.filter( (task) => {
                        return task !== this.tasks[id];
                });
            },
            add(e){
                if(!e.target.hasAttribute('disabled')){

                    if(!this.editig){
                        this.tasks.push(this.task);
                        this.task = '';
                    }else{
                        this.tasks[this.editId] = this.task;
                        this.task = '';
                    }

                }
            },
            showForm(){
                this.show = !this.show;
                let body = document.getElementsByName('body');
                console.log(body);
            },
            showAddingForm(){
                this.addin = !this.addin;
                this.editing = false;
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
