        border-radius: .8rem;
        background-color:rgba(201, 201, 201,.5);
        cursor: pointer;
        -webkit-box-shadow: .1px .6px 6px .1px #9e9e9e;
        box-shadow: .1px .6px 6px .1px #9e9e9e;
