        position:absolute;
        left:18px;
        bottom:16px;
    }
    .taski{
        border:1px solid black;
        border-radius:.5rem;
        /* height: 56px; */
        padding:.6rem 0rem .6rem .6rem;

        display:flex;
        justify-content: space-between;
    }
    .task-icons{
        width:20%;
        position:relative;
